:root {
    --card-spacing: 1.5vh;
}

.card-deck {
    margin: 5vh auto;
    display: grid;
    grid-gap: var(--card-spacing);
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: 1fr;
}

.card {
    position: relative;
    width: 100%;
    aspect-ratio: 1;
    perspective: 200px; 
    perspective-origin: 50% 50%;
}

.card-bg {
    width: 100%;
    aspect-ratio: 1;
    background-size: 100%, 100%;
    border-radius: 5px;
    transition: transform 200ms ease-in-out;
    box-shadow: 5px 5px 5px #010101aa;
    overflow: hidden;
}

.card:hover {
    z-index: 1;
}

.card-overlay {
    width: 100%;
    aspect-ratio: 1;
    mix-blend-mode: lighten;
    transition: background-position 200ms ease-in-out;
    background-image: linear-gradient(10deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.04) 46%, rgba(255,255,255,0.10) 65%, rgba(255,255,255,0.17) 72%, rgba(255,255,255,0.064) 100%);
    background-size: 100% 150%;
    background-position: bottom;
}

.card:hover .card-overlay {
    background-position: 0;
}

/* .card-bg > a > * {
    transition: opacity 200ms;
    opacity: 0;
} */
.card-header {
    transition: opacity 200ms ease-in-out;
    opacity: 0;
}

/* .card:hover .card-bg > a > * {
    opacity: 1;
} */
.card:hover .card-header {
    opacity: 1;
}

.card:hover .card-bg {
    transform-style: preserve-3d;
    transform: rotateX(5deg) translateZ(10px);
    box-shadow: 5px 5px 5px #010101aa;
}

.card-header {
    position: absolute;
    z-index: 10;
    margin: 0;
    padding: 5px;
    border-radius: 4px;
    top: 50%; 
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, .6);
    text-align: center;
}