body {
    margin: 0;
    background-color: #121212;
}

.section-header {
    margin: 0 auto;
    color:white;
    font-family: 'Cutive Mono', monospace;
    font-size: 4vw;
    width: max-content;
    margin-bottom: 0;
}

.header-underline {
    height: 6px;
    animation:  background-color-cycle 10s linear 0s infinite normal none,
                shadow-color-cycle 10s linear 0s infinite normal none;
}

.text-header {
    margin-bottom: 0;
    font-family: 'Cutive Mono', monospace;
    color:white;
}

.text-body {
    color:white;
    font-family: 'Roboto', sans-serif;
}

#bg-anim {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: -1;
}

