/* mobile */
@media all and (max-width: 767.9px) {
    .content-container {
        width: 100%;
    }

    .card-deck {
        width: 250px;
        display: flex;
        flex-direction: column;   
    }

    .card {
        margin-top: 30px;
    }

    .section-header {
        font-size: 30px;
    }

    .scale-text-header {
        font-size: 20px;
    }

    .scale-text-body {
        font-size: 16px;
    }

    .splash-container {
        height: 100vh;
    }

    .splash-text-container {
        top: 40%;
    }

    .splash-text-container h1 {
        font-size: 10vw;
    }

    .splash-text-container h2 {
        font-size: 4.5vw;
    }
}

/* larger screens */
@media all and (min-width: 768px) {
    .content-container {
        width: 80%;
    }

    .card-deck {
        width: 400px;
    }
    
    .section-header {
        font-size: 40px;
    }

    .scale-text-header {
        font-size: 30px;
    }

    .scale-text-body {
        font-size: 20px;
    }
}

@media all and (min-width: 1024px) {
    .content-container {
        width: 80%;
    }

    .card-deck {
        width: 600px;
    }

    .section-header {
        font-size: 60px;
    }

    .scale-text-header {
        font-size: 50px;
    }

    .scale-text-body {
        font-size: 25px;
    }
}